import Header from "./../components/header/Header"
import { animateLetters } from "./../components/animations/GSAPAnimations"
import { useEffect } from "react";

const Home = () => {
  useEffect(() => {
    animateLetters('.word', 1);
  }, []);  
  return ( 
        <div>
        <Header />

        <main className="section">
          <div className="container">
    
    
            <ul className="content-list">
              <li className="content-list__item">
                <h2 className="title-2 sklp"><span className="word">Skills</span></h2>
                <p>
                HTML, CSS, JavaScript, ReactJS, TailwindCSS, SQL
                </p>
                <img 
                src="https://www.codewars.com/users/Kotyi/badges/large" 
                alt="Codewars Badge" 
                />
              </li>
            </ul>
          </div>
        </main>
        </div>
     );
}
 
export default Home;